import axios from 'axios';

export const payCheckout = async data => {
  try {
    const response = await axios.post(
      'https://stellarlikes.com/api.php?action=payCheckout',
      data
    );

    console.log(response.data);
    return response.data;
  } catch (error) {
    console.error('   :', error);
    throw new Error('    .');
  }
};
